import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http' 
import {Game} from '../models/Game'
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class GamesService {
  API_URI='https://de17x5bjml.execute-api.us-east-1.amazonaws.com/test'
  constructor(private http: HttpClient) { }
  getGames(){
    return this.http.get(this.API_URI,{responseType: 'text'});
  }
  getGame(id:String){
    return this.http.get(this.API_URI+'?'+id,{responseType:'text'});
  }
  saveGame(game:Game){
    return this.http.post(this.API_URI+'/games/',game);
  }
  deleteGame(id:String){
    return this.http.delete(this.API_URI+'?'+id,{responseType:'text'});
  }
  updateGame(id: String,updatedGame: Game): Observable<Game>{
    return this.http.put(this.API_URI+'/games/'+id, updatedGame) ; 
  }

  
}
