import { Component, OnInit } from '@angular/core';
import {GamesService} from '../../services/games.service';
import {NgForm} from '@angular/forms';
@Component({
  selector: 'app-game-list',
  templateUrl: './game-list.component.html',
  styleUrls: ['./game-list.component.css']
})
export class GameListComponent implements OnInit {
  games: any =[]; 
  public show:boolean = false;
  public buttonName:any = 'Get data';
  public getData:any='Search'
  public content:any
  public validate:boolean=false;
  constructor(private gamesService:GamesService) { }


  hideButton(){
    this.validate=false;
  }

  deleteFile(data: String){
    this.gamesService.deleteGame(data).subscribe(
      res=> {
        console.log(String(res));
      },
      err=>{
        console.log(err);
        alert('No existe el archivo');
      }
    );
  }
  onSubmit(f: NgForm) {
    if(f.value.filename!=''){
    this.gamesService.getGame(f.value.filename).subscribe(
      res=> {
        this.content=String(res);
        console.log(this.content);
        this.validate=true;
      },
      err=>{
        console.log(err);
        alert('No existe el archivo');
      } 
    );
    }else{
      alert('Ingrese un valor');
    }
  }

  toggle() {
    this.show = !this.show;

    // CHANGE THE NAME OF THE BUTTON.
    if(this.show)  
      this.buttonName = "Hide data";
    else
      this.buttonName = "Get data";
  }

  ngOnInit(): void {
    this.gamesService.getGames().subscribe(
      res=> {
        this.games=String(res).split(',');
      },
      err=> console.log(err)
    );
  }

}
